.cinema-selector {
  margin-bottom: 1.5em;
}

.cinema-selector .selector {
  width: 100%;
  border: 1px solid white;
  border-radius: 10px;
  background-color: transparent;
  color: white;
  text-align: center;
  padding: 0.25em 0;
  font-size: 1.1em;
}

.cinema-selector > span {
  display: block;
  font-size: 1.25em;
  font-weight: bold;
  margin-bottom: 0.5em;
}

@media (min-width: 800px) {
  .cinema-selector .selector {
    width: 300px;
  }
}
