.datetime-selection-container {
  margin-bottom: 1.5em;
}

.datetime-selection {
  margin-bottom: 0.25em;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1em;
}

.datetime-selection > input {
  font-size: 1.2em;
  border: 1px solid white;
  color: white;
  background-color: transparent;
}

.datetime-selection > input[type="date"]::-webkit-calendar-picker-indicator {
  filter: invert(1);
}

.datetime-selection > input[type="time"]::-webkit-calendar-picker-indicator {
  filter: invert(1);
}

.datetime-selection-container .error {
  color: red;
}
