.content {
  padding: 0 1em;
}

.description {
  margin: 1em 0;
  margin-bottom: 1.5em;
  padding: 0 5em;
  display: block;
}

.search {
  width: 100%;
  border: 1px solid white;
  border-radius: 10px;
  background-color: transparent;
  color: white;
  text-align: center;
  padding: 0.25em 0;
  font-size: 1.1em;
  cursor: pointer;
}

@media (min-width: 800px) {
  .search {
    width: 200px;
  }
}

@media (max-width: 700px) {
  .description {
    padding: 0;
  }
}

.content > .error {
  color: red;
  display: block;
  margin-bottom: 3em;
}
