.movie-selection, .orderings {
  margin-bottom: 3em;
}

.movie-selection  > span, .orderings > span {
  display: block;
  font-size: 1.25em;
  font-weight: bold;
}

.movie-selection  > span, .orderings .start-end {
  margin-bottom: 0.5em;
}

.movie-selection .types {
  margin-bottom: 1em;
  display: flex;
  justify-content: center;
  gap: 1em;
}

.movie-selection .movies {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 1em;
}

.movie-selection .movies .movie-tile {
  transition: all 0.5s ease;
  display: block;
  position: relative;
  border: 1px solid black;
  cursor: pointer;
}

.movie-selection .movies .movie-tile:hover {
  border: 1px solid white;
}

.movie-selection .movies .movie-tile > img {
  width: 190px;
}

.movie-selection .movies .movie-tile > span {
  display: block;
}

.movie-selection .movies .movie-tile .small {
  display: block;
  color: grey;
  font-size: 0.9em;
}

.movie-selection .movies .movie-tile .selected {
  position: absolute;
  top: 2px;
  right: 2px;
  font-size: 1.5em;
  background-color: black;
  color: white;
  font-weight: bold;
  padding: 5px;
  border-radius: 50%;
  height: 25px;
  width: 25px;
  border: 1px solid white;
}

.orderings .viewing-order {
  display: flex;
  justify-content: center;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 2em;
}

.orderings .viewing-order .ordering-tile {
  transition: all 0.5s ease;
  display: block;
}

.orderings .viewing-order .ordering-tile > img {
  width: 150px;
}

.orderings .viewing-order .ordering-tile > span {
  display: block;
}

.orderings .viewing-order .ordering-tile .small {
  display: block;
  margin-bottom: 0.4em;
  color: grey;
  font-size: 0.9em;
}

.orderings .viewing-order .ordering-tile .time-type {
  margin-bottom: 0.2em;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.2em;
}

.orderings .start-end .start {
  display: inline-block;
  border: 2px solid #e52729;
  color: white;
  text-decoration: none;
  padding: 2px 1px;
  min-width: 44px;
  text-align: center;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.orderings .start-end .end {
  display: inline-block;
  border: 2px solid grey;
  color: white;
  text-decoration: none;
  padding: 2px 1px;
  min-width: 44px;
  text-align: center;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
}

.orderings .viewing-order .ordering-tile > div > a {
  display: inline-block;
  border: 2px solid #e52729;
  color: white;
  text-decoration: none;
  padding: 4px 3px;
  min-width: 48px;
  text-align: center;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  flex: 1 1 0;
}

.orderings .viewing-order .ordering-tile > div .ends {
  display: inline-block;
  border: 2px solid grey;
  color: white;
  text-decoration: none;
  padding: 4px 3px;
  min-width: 48px;
  text-align: center;
  border-radius: 4px;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  flex: 1 1 0;
}

.orderings .viewing-order .ordering-tile > div .type {
  font-weight: bold;
  font-size: 0.9em;
  flex: 1 1 0;
}
