.header {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Arial';
  font-weight: bold;
  margin-top: 1em;
  border-bottom: 1px solid white;
}

.header > img {
  height: 75px;
  padding-bottom: 1em;
}

.header > span {
  display: block;
  font-size: 3em;
  text-transform: lowercase;
}

@media (max-width: 450px) {
  .header > span {
    font-size: 2.5em;
  }
  .header > img {
    height: 60px;
  }
}
